import {NavLink} from "react-router-dom";

import {RouterEndpoints} from "../../routes";
import {UserInfoPage} from "../../pages";

const Header = () => {

    return (
        <>
            <NavLink to={''}></NavLink>
            <NavLink to={RouterEndpoints.osbb}> </NavLink>
            <NavLink to={RouterEndpoints.rent_and_services}>  </NavLink>
            <NavLink to={RouterEndpoints.news_and_events}>  </NavLink>
            <NavLink to={RouterEndpoints.profile}> </NavLink>
            <NavLink to={RouterEndpoints.login}></NavLink>
            <NavLink to={RouterEndpoints.register}></NavLink>
            <UserInfoPage/>
        </>
    );
};

export {Header};